.dataTable {
  margin: 10px;
  margin-left: 95px;
  font-size: 11px;
  overflow: auto;
}
.dataTable td {
  max-width: 75px;
  padding: 5px;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  vertical-align: top;
}
.dataTable td.schema {
  position: absolute;
  left: 10px;
  top: auto;
  min-width: 75px;
  border-radius: 0;
  border-width: 1px;
  border-style: solid;
  background: #bed9ab;
  border-color: rgba(140, 161, 124, 0.5);
  cursor: pointer;
}
.dataTable td:not(.schema).even {
  background: #eee;
  border-bottom: 1px dotted #ddd;
}
.dataTable td:not(.schema).odd {
  background: #fff;
  border-bottom: 1px dotted #eee;
}
.paging {
  font-size: 11px;
}
.pager {
  float: right;
}



