.editor-container {
  height: 100%; width: 100%; display: -ms-flexbox; display: flex;  -ms-flex-flow: column;  flex-flow: column;
  margin-left: 10px;
  margin-top: 10px;
}

.relative-container {
  overflow-y: auto; width:100%; height: 100%; border:black solid thin; position: relative;
}

.absolute-container {
  position: absolute; top: 0; bottom: 0; width: 100%; height: 100%;
}


.editor-display-errors {
  font-size: 0.8em;
  margin: 0;
  color: red;
}

.editor-display-warns {
  font-size: 0.8em;
  margin: 0;
  color: orange;
}



