.exampleTable {
  margin: 5px;
  margin-left: 10px;
  font-size: 11px;
  overflow: auto;
}
.exampleTable td {
  max-width: 350px;
  padding: 5px;
  white-space: nowrap;
  overflow: scroll;
  /* text-overflow: ellipsis; */
  vertical-align: top;
}

.exampleTable td:hover{
  overflow: visible;
  /* white-space: pre; */
}

.exampleTable tr.even {
  background: #eee;
  border-bottom: 1px dotted #ddd;
}
.exampleTable tr.odd {
  background: #fff;
  border-bottom: 1px dotted #eee;
}

.exampleTable th {
  cursor: pointer;
  text-decoration: underline;
  color: #ed490a;
}



