.Header {
  position: fixed;
  width: 100%;
  height: 50px; /* Mirrored by .container padding-bottom */
  background-color: #24292e;
  -webkit-box-shadow: 0 4px 16px rgba(0,0,0,0.2);
          box-shadow: 0 4px 16px rgba(0,0,0,0.2);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 0 5%;
  color: #FFF;
  font-size: 1.8rem;
  top: 0;
  z-index: 100;
}

.Header .Bigger {
  height: 100px;
}

.Header-logo {
  font-weight: 900;
}

.Header-sublogo {
  font-weight: 100;
  margin-left: 0.5em;
}

.Header-nav {
  -ms-flex-item-align: end;
      align-self: flex-end;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
}

.Header-nav div {
  display: -ms-inline-flexbox;
  display: inline-flex;
  height: 100%;
  width: auto;
  padding: 0 15px;
  font-size: 1.3rem;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
}

.Header-nav div:hover {
  background-color: rgba(244, 244, 244, 0.5);
  cursor: pointer;
}

.Header-nav a {
  text-decoration: none;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: inherit;
  height: 100%;
  color: rgba(255, 255, 255, 0.9);
}

.Header-nav a.active div {
  background-color: transparent;
  cursor: default;
  font-weight: 700;
  color: #FFF;
}

.Header-nav a.active div:after {
  content: " ";
  width: 100%;
  position: absolute;
  height: 4px;
  background-color: rgba(255, 255, 255, 0.9);
  bottom: 0;
  left: 0;
}

.Header-code {
    font-size: 0.7em;
}


.Header-info {
    font-size: 0.8em;
}



